import { Component } from '@angular/core';
import { AlertController,IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
@IonicPage()
@Component({
  selector: 'page-cursoslibres2-list',
  templateUrl: 'cursoslibres2-list.html',
})
export class Cursoslibres2ListPage {
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  pingreso:boolean=false;
    pconsulta:boolean=false;
    peliminacion:boolean=false;
    pcreacion:boolean=false;
    pactualizacion:boolean=false;
    preporte:boolean=false;
    pfiltro:boolean=false;
    permisom: any=[];
    pet:any;
    idempresa:any;
    idafiliado:any='na';
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";

  displayQrCode2(item) {


    var link=this.regService.daserverenuso()+ '/?key=1'+ '&key2=' +  item._id+ '&key3=' +  item.capacidad
    this.regService.abrelinkinterno3(link,'_blank','',0)
    }



  constructor(public storage: Storage, public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {

      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })

    this.pet = "Cursos libres en linea";
    this.estadoant='Cursos libres en linea'
      this.permisom = String(navParams.get("permiso")).split(",");
      this.pingreso=(  this.permisom[0] == 'true');
      this.pconsulta=(  this.permisom[1] == 'true');
      this.peliminacion=(  this.permisom[2] == 'true');
      this.pcreacion=(  this.permisom[3] == 'true');
      this.pactualizacion=(  this.permisom[4] == 'true');
      this.pfiltro=(  this.permisom[5] == 'true');
      this.preporte=(  this.permisom[6] == 'true');

  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  estadoant:any;

  busca(op:any) {
    this.estadoant=op
    this.pideregistros();

      }

  showFilter()
  {





                    let select = {
                      title: 'Seleccione area',
                      message: 'Estado',    inputs: [],
                      buttons: [           {            text: 'Cancelar'     },    {       text: 'Buscar',handler: todo => {

if(todo=='TODOS')
{

  this.regService.getRegsintok(this.idempresa + '/Publicación/'+this.estadoant,'area_eventos').then((data) => {
    this.items=data;

}, (err) => {
this.msj.showError3('Error',err.error);
// this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
});

}
else
{
  this.regService.getRegsintok(this.idempresa + '/Publicación/'+this.estadoant +'/' + todo,'area_eventos').then((data) => {
    this.items=data;

}, (err) => {
this.msj.showError3('Error',err.error);
// this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
});

}




                        }  } ]
                  }

                  select.inputs.push({name: 'myInput', type: 'radio',label:'ÁREA DE  SALUD Y PSICOPEDAGOGÍA',value:'ÁREA DE  SALUD Y PSICOPEDAGOGÍA'});
                  select.inputs.push({name: 'myInput', type: 'radio',label:'ÁREA TÉCNICA',value:'ÁREA TÉCNICA'});
                  select.inputs.push({name: 'myInput', type: 'radio',label:'ÁREA DE CIENCIAS JURÍDICAS Y SOCIALES, CIENCIA POLÍTICA Y CIENCIAS SOCIALES',value:'ÁREA DE CIENCIAS JURÍDICAS Y SOCIALES, CIENCIA POLÍTICA Y CIENCIAS SOCIALES'});
                  select.inputs.push({name: 'myInput', type: 'radio',label:'CURSOS DE INTERES MULTIPLE',value:'CURSOS DE INTERES MULTIPLE'});
                  select.inputs.push({name: 'myInput', type: 'radio',label:'ÁREA DE  SALUD Y PSICOPEDAGOGÍA',value:'ÁREA DE  SALUD Y PSICOPEDAGOGÍA'});
                  select.inputs.push({name: 'myInput', type: 'radio',label:'TODOS',value:'TODOS'});


                let newAlert = this.alertCtrl.create(select);
                newAlert.present();








  }

  pideregistros() {
      let self = this;
      self.loading = true;


        this.regService.getRegsintok(this.idempresa + '/Publicación/'+this.estadoant,'area_eventos').then((data) => {
          this.items=data;
          self.loading = false;
      }, (err) => {
      this.msj.showError3('Error',err.error);
      // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
      });


  }
  ionViewDidLoad() {
  //  this.estadoant=op
  this.estadoant='Cursos libres en linea'
    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
          let recordID 		: string		= item._id;

          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'area_eventos').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });


        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();




  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('Cursoslibres2CreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('Cursoslibres2CreatePage',{ record : null },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }

}



